body {
    background-color: #121212; /* Темный фон для всего документа */
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
  }
  
  .Instinct {
    text-align: center;
    padding: 20px;
    background-color: #121212; /* Темный фон */
    color: #ffffff; /* Белый текст */
  }
  
  .talents {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 20px; /* Промежуток между контейнерами */
    justify-content: center;
  }
  
  .talent {
    text-align: center;
    background-color: #1e1e1e; /* Цвет контейнера */
    border-radius: 8px;
    padding: 20px;
    box-sizing: border-box; /* Учитываем padding и border в ширине и высоте */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  .talent img {
    width: 100%;
    height: 200px; /* Увеличенная высота изображения */
    border-radius: 15px; /* Закругленные края */
    object-fit: cover;
  }
  
  .talent h2 {
    margin-top: 10px;
    font-size: 1.2em;
    color: #ffffff;
    flex-grow: 1; /* Заголовок по центру контейнера */
  }
  
  .talent button {
    margin-top: 10px;
    padding: 10px;
    font-size: 1em;
    cursor: pointer;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 4px;
    width: 100%; /* Кнопка занимает всю ширину контейнера */
    box-sizing: border-box; /* Учитываем padding и border в ширине и высоте */
    align-self: flex-end; /* Кнопка в нижней части контейнера */
  }
  
  .talent-info {
    text-align: center;
  }
  
  .talent-info img {
    width: 100%;
    height: auto;
    border-radius: 15px; /* Закругленные края */
    object-fit: cover;
  }
  
  .talent-info h2 {
    margin-top: 10px;
    font-size: 1.5em;
    color: #ffffff;
  }
  
  .talent-info p {
    margin-top: 10px;
    font-size: 1em;
    color: #cccccc;
  }
  
  .talent-info button {
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 1em;
    cursor: pointer;
    background-color: #f44336;
    color: white;
    border: none;
    border-radius: 4px;
  }
  
  .additional-images {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-top: 20px;
  }
  
  .additional-images img {
    width: 150px;
    height: 150px;
    border-radius: 15px; /* Закругленные края */
    object-fit: cover;
    border: 2px solid #ffffff; /* Белая рамка */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Тень */
  }
  
  .menu-button {
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 1em;
    cursor: pointer;
    background-color: #2196F3;
    color: white;
    border: none;
    border-radius: 4px;
  }
  
  @media (max-width: 600px) {
    .talents {
      grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    }
  
    .talent {
      width: 100%;
    }
  
    .talent img {
      width: 100%;
      height: auto;
    }
  
    .additional-images {
      flex-direction: column;
    }
  
    .additional-images img {
      width: 100%;
    }
  }
  